import { Request, Response } from 'express';
import { ResponsePattern } from '@/interfaces/response.interface';
import { message } from '@/constants/messages';

import ShiftService from '@/services/shift.service';
import SlipService from '@/services/slip.service';
import { create } from 'domain';

class ShiftController {
  public shiftService = new ShiftService();
  public slipService = new SlipService();

  public closeShift = async (req: Request, res: Response): Promise<void> => {
    const { body } = req;
    let nextShift, business_date;
    try {
      const { data } = body;
      const result = await this.shiftService.closeShiftOrDate(data, 'shift');
      res.status(result.statusCode).json(result);
    } catch (e) {
      console.log(e);
      res.status(400).json({ statusCode: 400, message: message.errors.badRequest });
    }
  };

  public closeDate = async () => {};
}

export default ShiftController;
